import React from "react";
import { StaticImage } from "gatsby-plugin-image";

const OurService = () => {
  return (
    <section className="section-service py-5" id="how">
      <div className="row mb-5">
        <h3 className="heading-tertiary text-center">OUR SERVICES</h3>
      </div>
      <div className="container">
        <div className="row gap-3 d-flex justify-content-around align-items-center">
          <div className="rightAnimation maincontainer col-md-4 mb-5">
            <div className="thecard shadow">
              <div className="thefront d-flex flex-column align-items-center justify-content-center">
                <StaticImage
                  src="../../styles/assets/images/ServiceLogo/webdevelop.png"
                  alt="Web development Logo"
                />

                <h4>Web Development</h4>
              </div>
              <div className="theback p-4">
                <p className="fw-bold">Web Development</p>
                <p>
                  Our team of expert developers leverages the latest
                  technologies and industry best practices to deliver websites
                  that are not only visually captivating but also highly
                  functional and user-friendly. Whether it's a dynamic
                  e-commerce platform, a sleek corporate site, or a custom web
                  application, we ensure pixel-perfect design, seamless
                  performance, and robust security. Your success is our code.
                </p>
              </div>
            </div>
          </div>
          <div className="rightAnimation maincontainer col-md-4 mb-5">
            <div className=" thecard shadow">
              <div className="thefront d-flex flex-column align-items-center justify-content-center">
                <StaticImage
                  src="../../styles/assets/images/ServiceLogo/smart_contract.png"
                  alt="Smart Contract Development"
                />

                <h4>Smart Contract Development</h4>
              </div>
              <div className="theback p-4">
                <p className="fw-bold">Smart Contract Development</p>
                <p>
                  Our experienced developers utilize cutting-edge blockchain
                  platforms like Ethereum and Solidity to design and deploy
                  smart contracts that facilitate efficient, error-free
                  agreements and transactions. From decentralized applications
                  (DApps) to token creation and ICO smart contracts, we empower
                  businesses to innovate, reduce costs, and enhance security.
                </p>
              </div>
            </div>
          </div>
          <div className="rightAnimation maincontainer col-md-4 mb-5">
            <div className="thecard shadow">
              <div className="thefront d-flex flex-column align-items-center justify-content-center">
                <StaticImage
                  src="../../styles/assets/images/ServiceLogo/opensource.png"
                  alt="POC Open source Development"
                />

                <h4>POC Open Source Development</h4>
              </div>
              <div className="theback p-4">
                <p className="fw-bold">POC Open Source Development</p>
                <p>
                  We design and implement POCs to validate ideas, mitigating
                  risks and ensuring project feasibility. Additionally, we
                  leverage the power of open source technologies to create
                  efficient, customizable, and collaborative software solutions.
                  Our expertise spans across various domains, delivering
                  impactful results and fostering community-driven innovation.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default OurService;
