import React from "react";

import { Container, Row, Col } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination } from "swiper/modules";

const Banner = () => {
  return (
    <section className="main_hero_sec">
      <Container>
        <Row>
          <Col md={12}>
            <div className="hero_center_div">
              <div className="hero_banner_title">
                <div className="hero_title_sec">
                  <Swiper
                    modules={[Autoplay]}
                    direction={"vertical"}
                    speed={900}
                    autoplay={{
                      delay: 1500,
                      disableOnInteraction: false,
                    }}
                    loop={true}
                    slidesPerView={1}
                    className="mySwiper"
                  >
                    <SwiperSlide>
                      <h1>NFT Marketplace</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Web3</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Crypto Exchange</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Blockchain</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Game</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Metaverse</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Defi</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Wallet</h1>
                    </SwiperSlide>
                    <SwiperSlide>
                      <h1>Dapp</h1>
                    </SwiperSlide>
                  </Swiper>
                </div>
                <h2 className="stable_text">Development Company</h2>
              </div>
              <p>
                Our Team specializes in modernizing and automating industries,
                enterprises, and Businesses with modern digital transformation
                technology solutions like blockchain, AI, Metaverse, Crypto,
                NFT, and Web3.
              </p>
              <div className="main_hero_btns_group">
                <a href="#cta" className="primary_btn">
                  Get Started
                </a>
                <a href="#how" className="secondary_btn">
                  Learn more
                </a>
              </div>
              <div className="main_hero_first_ellipse"></div>
              <div className="main_hero_second_ellipse"></div>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Banner;
