import * as React from "react";
import Layout from "../components/layout";
import Banner from "../screens/main/banner";
import Popular from "../screens/main/popular";
import AboutUs from "../screens/main/about";
import OurService from "../screens/main/our-service";
import OurDevelopment from "../screens/main/our-development";
import RecentWorks from "../screens/main/recent-works";
import OurTeam from "../screens/main/our-team";
import MoveForward from "../components/common/move-forward";
import Seo from "../components/seo";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";

const IndexPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);

  return (
    <Layout db={db}>
      <Banner />
      {/* <Popular /> */}
      <AboutUs />
      <OurService />
      <OurDevelopment />
      <RecentWorks />
      <OurTeam />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Home"
    description="Justtry Technologies, a trailblazing software development company, pioneers cutting-edge solutions for diverse industries. With a focus on innovation and client satisfaction, we deliver bespoke software that transforms businesses. Our expertise spans web and mobile applications, AI, and more, ensuring seamless integration of technology to drive success."
    keywords="Custom Software Development, Innovative IT Solutions, Expert Web Development, Mobile App Development Services, Artificial Intelligence Solutions, Bespoke Software Solutions, Technology Integration Company, Client-Centric Software Development, Industry-leading IT Services, Software Solutions for Business Success"
  ></Seo>
);

export default IndexPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
