import React from "react";
import { StaticImage } from "gatsby-plugin-image";
import { Swiper, SwiperSlide } from "swiper/react";
import {
  Navigation,
  Scrollbar,
  A11y,
  EffectFade,
  Autoplay,
} from "swiper/modules";
import { BreakPoints } from "../../core/const";

const OurTeam = () => {
  return (
    <section className="mb-5">
      <div className="row">
        <h3 className="text-center pb-5 text-uppercase">
          proudly introduce our development team
        </h3>
      </div>
      <div className="container">
        <Swiper
          modules={[Navigation, Scrollbar, A11y, EffectFade, Autoplay]}
          slidesPerView={1}
          spaceBetween={30}
          loop={true}
          autoplay={true}
          breakpoints={BreakPoints}
          className="mySwiper"
        >
          <SwiperSlide>
            <div className="swiper_card">
              <div className="swiper_card_img_box">
                <StaticImage
                  src="../../styles/assets/images/Teammates/Pravin.png"
                  width={120}
                  alt="Team Member"
                />
              </div>
              <div className="swiper_card_body">
                <div className="swiper_card_title">
                  <h4>Pravin Raj</h4>
                  <p>Full Stack Developer</p>
                </div>
                <div className="swiper_card_description">
                  <p>
                    As a fullstack developer, He has 8 years of experience in
                    building and maintaining web applications. His skills in
                    languages such as HTML, CSS, JavaScript, and Php, as well as
                    frameworks such as React, Angular, NextJs, ExpressJs and
                    NestJS, created efficient and user-friendly applications
                    from start to finish. He design, develop platforms with
                    databases or servers.
                  </p>
                </div>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="swiper_card">
              <div className="swiper_card_img_box">
                <StaticImage
                  src="../../styles/assets/images/Teammates/Sudharsan.png"
                  width={120}
                  alt="Team Member"
                />
              </div>
              <div className="swiper_card_body">
                <div className="swiper_card_title">
                  <h4>Sudharsan</h4>
                  <p>UI/UX Designer</p>
                </div>
                <div className="swiper_card_description">
                  <p>
                    He is implementing design and interaction guidelines to
                    enhance usability and accessibility across devices and
                    platforms, staying updated with industry trends and emerging
                    technologies to continuously enhance UI/UX practices, and
                    Communicating effectively within cross-functional teams to
                    deliver cohesive and user-friendly digital products.
                  </p>
                </div>
              </div>
            </div>
          </SwiperSlide>

          <SwiperSlide>
            <div className="swiper_card">
              <div className="swiper_card_img_box">
                <StaticImage
                  src="../../styles/assets/images/Teammates/Vijay.png"
                  width={120}
                  alt="Team Member"
                />
              </div>
              <div className="swiper_card_body">
                <div className="swiper_card_title">
                  <h4>Vijay</h4>
                  <p>Front End Developer</p>
                </div>
                <div className="swiper_card_description">
                  <p>
                    As a front-end developer, His passion for building beautiful
                    and intuitive user experiences. With a strong foundation in
                    HTML, CSS, and JavaScript, He delivers high-quality web
                    projects on time and on budget. He always looking for new
                    challenges and opportunities to improve skills and
                    contribute to the success of his team and clients.
                  </p>
                </div>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="swiper_card">
              <div className="swiper_card_img_box">
                <StaticImage
                  src="../../styles/assets/images/Teammates/Prasanna.png"
                  width={120}
                  alt="Team Member"
                />
              </div>
              <div className="swiper_card_body">
                <div className="swiper_card_title">
                  <h4>Prasanna</h4>
                  <p>Back End Developer</p>
                </div>
                <div className="swiper_card_description">
                  <p>
                    As a backend developer, He specialized in building and
                    maintaining efficient and scalable server-side systems. With
                    experience in languages such as JavaScript and Php, as well
                    as databases like MySql and MongoDb. He skilled at
                    integrating with various APIs and databases to create
                    seamless and user-friendly web applications.
                  </p>
                </div>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="swiper_card">
              <div className="swiper_card_img_box">
                <StaticImage
                  src="../../styles/assets/images/Teammates/Anbu.png"
                  width={120}
                  alt="Team Member"
                />
              </div>
              <div className="swiper_card_body">
                <div className="swiper_card_title">
                  <h4>Anbarasan</h4>
                  <p>QA Engineer</p>
                </div>
                <div className="swiper_card_description">
                  <p>
                    Ensuring the quality and functionality of software products
                    by conducting through testing and identifying defects. They
                    create test plans, execute test cases, and document
                    findings. Also collaborate with development teams to
                    communicate issues, verify bug fixes, and ensure the
                    software meets quality standards before release.
                  </p>
                </div>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="swiper_card">
              <div className="swiper_card_img_box">
                <StaticImage
                  src="../../styles/assets/images/Teammates/Karthick.png"
                  width={120}
                  alt="Team Member"
                />
              </div>
              <div className="swiper_card_body">
                <div className="swiper_card_title">
                  <h4>Karthick</h4>
                  <p>UI Developer</p>
                </div>
                <div className="swiper_card_description">
                  <p>
                    Designs and creates user interfaces for digital products,
                    ensuring they are visually appealing, intuitive, and
                    responsive. They collaborate with UX designers and front-end
                    developers to translate design concepts into code using
                    HTML, CSS, and JavaScript. also optimize interfaces for
                    seamless user interactions and stay updated with design
                    trends and best practices to enhance user experiences.
                  </p>
                </div>
              </div>
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
    </section>
  );
};

export default OurTeam;
