import React from "react";
import { utils } from "../../core/helper";

const Popular = () => {
  return (
    <section className="section-featured p-5">
      <div className="container">
        <div className="row">
          <h4 className="rightAnimation heading-fourth text-center">
            POPULAR IN
          </h4>
        </div>
        <div className="container">
          <div className="row p-5">
            <div className="col-lg-2 col-md-4 col-sm-4 col-6 ">
              <div className="popular_tech">
                {utils.react(125, 125)}
                <p className="feature-description">React</p>
              </div>
            </div>
            <div className="col-lg-2 col-md-4 col-sm-4 col-6 ">
              <div className="popular_tech">
                {utils.javascript(125, 125)}
                <p className="feature-description">Javascript</p>
              </div>
            </div>
            <div className="col-lg-2 col-md-4 col-sm-4 col-6 ">
              <div className="popular_tech">
                {utils.angular(125, 125)}
                <p className="feature-description">Angular</p>
              </div>
            </div>
            <div className="col-lg-2 col-md-4 col-sm-4 col-6 ">
              <div className="popular_tech">
                {utils.nodejs(125, 125)}
                <p className="feature-description">Node JS</p>
              </div>
            </div>
            <div className="col-lg-2 col-md-4 col-sm-4 col-6 ">
              <div className="popular_tech">
                {utils.mongodb(125, 125)}
                <p className="feature-description">Mongo DB</p>
              </div>
            </div>
            <div className="col-lg-2 col-md-4 col-sm-4 col-6 ">
              <div className="popular_tech">
                {utils.solidity(125, 125)}
                <p className="feature-description">Solidity</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Popular;
